<template>
  <div class="out-box">
    <vueMarquee :data="msg" :style-object="styleObject"/>
  </div>
</template>
<script>
import vueMarquee from './components/vue-marquee'

export default {
  name: 'App',
  components: {
    vueMarquee
  },
  data() {
    return {
      msg: '君不见黄河之水天上来，奔流到海不复回，君不见高堂明镜悲白发，朝如青丝暮成雪，人生得意须尽欢，莫使金樽空对月',
      styleObject: {
        color: 'red',
        fontSize: '13px'
      }
    }
  }
}

</script>
<style>
.out-box {
  width: 300px;
  margin: 0 auto;
  border: 1px solid #ff6700;
}

</style>



